.Layout {
    background: #fff;
}

.Header {
    background: #455a64;
    padding: 0;
    line-height: 50px;
    height: 50px;
    box-shadow: 0px 0px 10px #ccc;
    z-index: 1000;
    position: fixed;
    width: 100%;
    padding: 4px;
    padding-left: 8px;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
}

.Main {
    background: #f0f2f5;
}

